.btn:hover,
.btn:active {
  text-shadow: none;
}

.btn {
  background: #252a34;
  border: none;
  font-size: 4rem;
  font-weight: bolder;

  cursor: pointer;
  outline: none;
}

.p1 {
  color: #ff2e63;
  text-shadow: 0px 0px 5px rgba(255, 0, 0, 0.9),
    0px 0px 15px rgba(255, 0, 0, 0.9);
  background: #801731;
}

.p2 {
  color: #08d9d6;
  text-shadow: 0px 0px 5px rgba(8, 217, 214, 0.9),
    0px 0px 15px rgba(8, 217, 214, 0.9);
  background: #045958;
}
